/* Stairing Brand Fonts */
@import url('https://fonts.googleapis.com/css?family=Baloo');
$baloo: 'Baloo', serif;

@import url('https://fonts.googleapis.com/css?family=Roboto+Slab:400,700');
$roboto-slab: 'Roboto Slab', serif;

@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,700');
$open-sans: 'Open Sans', sans-serif;

@import url("//netdna.bootstrapcdn.com/bootstrap/3.0.0/css/bootstrap-glyphicons.css");

/* Custom Bootstrap SASS */

$gray-base: rgb(46, 43, 43); /* #2E2B2B */
$gray-darker: lighten($gray-base, 13.5%);
$gray-dark: lighten($gray-base, 20%);
$gray: lighten($gray-base, 33.5%);
$gray-light: lighten($gray-base, 46.7%);
$gray-lighter: lighten($gray-base, 80%);

$brand-primary: rgb(237, 20, 22);
$brand-success: rgb(19, 188, 150);
$brand-info: rgb(34, 146, 164);
$brand-warning: rgb(240, 173, 78);
$brand-danger: darken($brand-primary, 13%);
$brand-offwhite: rgb(245, 237, 237);/* #F5EDED */
.text-brand-offwhite {color: $brand-offwhite;}
.text-brand-info-lighter {
  color: lighten($brand-info, 80%);
}
.text-brand-success {
  color: $brand-success;
}

$body-bg: $gray-lighter;
$text-color: $gray-dark;
$link-color: $brand-info;
$link-hover-color: $brand-success;
$link-hover-decoration: underline;

$font-family-sans-serif: $open-sans;
$font-family-serif: $roboto-slab;
$font-family-monospace: $baloo;
$font-family-base: $font-family-sans-serif;
$font-size-base: 14px;
$font-size-large: ceil(($font-size-base * 1.35)); //19pt
$font-size-small: ceil(($font-size-base * 0.72)); //10pt
$font-size-h1: floor(($font-size-base * 4.5)); //63pt
$font-size-h2: floor(($font-size-base * 2.62)); //39pt
$font-size-h3: ceil(($font-size-base * 1.18)); //19pt
$font-size-h4: $font-size-base;
$font-size-h5: ceil(($font-size-base * 0.86)); //12pt
$font-size-h6: ceil(($font-size-base * 0.72)); //10pt


$line-height-base: 1.428571429;
$line-height-computed: floor(($font-size-base * $line-height-base));
$line-height-large: 1.3333333;
$line-height-small: 1.5;

$headings-font-family: $font-family-monospace;
$headings-font-weight: 500;
$headings-line-height: 1.1;
$headings-color: $brand-info;

$icon-font-path: "../bower_components/boostrap-sass/assets/fonts/bootstrap/";
$icon-font-name: "../bower_components/bootstrap-sass/assets/fonts/bootstrap/glyphicons-halflings-regular";
$icon-font-svg-id: "../bower_components/bootstrap-sass/assets/fonts/bootstrap/glyphicons-halflings-regular";

$padding-base-vertical: 6px;
$padding-base-horizontal: 12px;
$padding-large-vertical: 10px;
$padding-large-horizontal: 16px;
$padding-small-vertical: 5px;
$padding-small-horizontal: 10px;
$padding-xs-vertical: 1px;
$padding-xs-horizontal: 5px;
.px {
  padding-left: 15px;
  padding-right: 15px;
}

$border-radius-base: 16px;
$border-radius-large: 22px;
$border-radius-small: 3px;
$component-active-color: rgb(245, 239, 237);
$component-active-bg: $brand-primary;
$caret-width-base: 4px;
$caret-width-large: 5px;

$table-cell-padding: 8px;
$table-condensed-cell-padding: 5px;
$table-bg: transparent;
$table-bg-accent: rgb(249, 249, 249);
$table-bg-hover: rgb(245, 245, 245);
$table-bg-active: $table-bg-hover;
$table-border-color: rgb(221, 221, 221);


/* BUTTONS */
$btn-font-weight: normal;
$btn-default-color: $brand-success;
$btn-default-bg: #FFF;
$btn-default-border: $brand-success;
$btn-primary-color: $brand-primary;
$btn-primary-bg: $btn-default-bg;
$btn-primary-border: darken($brand-primary, 5%);
$btn-success-color: #FFF;
$btn-success-bg: $brand-success;
$btn-success-border: darken($btn-success-bg, 5%);
$btn-info-color: $brand-info;
$btn-info-bg: $btn-default-bg;
$btn-info-border: darken($brand-info, 5%);
$btn-warning-color: $brand-warning;
$btn-warning-bg: $btn-default-bg;
$btn-warning-border: darken($brand-warning, 5%);
$btn-danger-color: #FFF;
$btn-danger-bg: $brand-danger;
$btn-danger-border: darken($brand-danger, 5%);
$btn-link-disabled-color: $gray-light;
$btn-border-radius-base: $border-radius-base;
$btn-border-radius-large: $border-radius-large;
$btn-border-radius-small: $border-radius-small;

$input-bg: rgb(255, 255, 255);
$input-bg-disabled: #eae7e7;
$input-color: $gray;
$input-border: rgb(204, 204, 204);
$input-border-radius: $border-radius-base;
$input-border-radius-large: $border-radius-large;
$input-border-radius-small: $border-radius-small;
$input-border-focus: rgb(102, 175, 233);
$input-color-placeholder: rgb(153, 153, 153);
$input-height-base: ($line-height-computed + ($padding-base-vertical * 2) + 2);
$input-height-large: (ceil($font-size-large * $line-height-large) + ($padding-large-vertical * 2) + 2);
$input-height-small: (floor($font-size-small * $line-height-small) + ($padding-small-vertical * 2) + 2);
$form-group-margin-bottom: 15px;
$legend-color: $gray-dark;
$legend-border-color: rgb(229, 229, 229);
$input-group-addon-bg: $gray-lighter;
$input-group-addon-border-color: $input-border;
$cursor-disabled: not-allowed;

$dropdown-bg: rgb(255, 255, 255);
$dropdown-border: rgba(0, 0, 0, 0.15);
$dropdown-fallback-border: rgb(204, 204, 204);
$dropdown-divider-bg: rgb(229, 229, 229);
$dropdown-link-color: $gray-dark;
$dropdown-link-hover-color: darken($gray-dark, 5%);
$dropdown-link-hover-bg: rgb(245, 245, 245);
$dropdown-link-active-color: $component-active-color;
$dropdown-link-active-bg: $component-active-bg;
$dropdown-link-disabled-color: $gray-light;
$dropdown-header-color: $gray-light;
$dropdown-caret-color: rgb(0, 0, 0);

$zindex-navbar: 1000;
$zindex-dropdown: 1000;
$zindex-popover: 1060;
$zindex-tooltip: 1070;
$zindex-navbar-fixed: 1030;
$zindex-modal-background: 1040;
$zindex-modal: 1050;

$screen-xs: 480px;
$screen-xs-min: $screen-xs;
$screen-phone: $screen-xs-min;
$screen-sm: 768px;
$screen-sm-min: $screen-sm;
$screen-tablet: $screen-sm-min;
$screen-md: 992px;
$screen-md-min: $screen-md;
$screen-desktop: $screen-md-min;
$screen-lg: 1200px;
$screen-lg-min: $screen-lg;
$screen-lg-desktop: $screen-lg-min;
$screen-xs-max: ($screen-sm-min - 1);
$screen-sm-max: ($screen-md-min - 1);
$screen-md-max: ($screen-lg-min - 1);

$grid-columns: 12;
$grid-gutter-width: 30px;
$grid-float-breakpoint: $screen-sm-min;
$grid-float-breakpoint-max: ($grid-float-breakpoint - 1);
$container-tablet: (720px + $grid-gutter-width);
$container-sm: $container-tablet;
$container-desktop: 100%;
$container-md: $container-desktop;
$container-large-desktop: 100%;
$container-lg: $container-large-desktop;


//* NAV *//
$navbar-height: 80px;
.container.main{
  margin-top: $navbar-height;
  margin-left: 0;
  margin-right: 0;
};
$navbar-margin-bottom: $line-height-computed;
$navbar-border-radius: 0;
$navbar-padding-horizontal: floor(($grid-gutter-width / 2));
$navbar-padding-vertical: (($navbar-height - $line-height-computed) / 2);
$navbar-collapse-max-height: 340px;
$navbar-default-color: #FFF;
$navbar-default-bg: #FFFFFF;
$navbar-default-border: $brand-primary;

$navbar-default-link-color: $brand-success;
$navbar-default-link-hover-color: $brand-info;
$navbar-default-link-hover-bg: lighten($brand-offwhite, 4%);
$navbar-default-link-active-color: $brand-success;
$navbar-default-link-active-bg: lighten($brand-offwhite, 2%);
$navbar-default-link-disabled-color: rgb(204, 204, 204);
$navbar-default-link-disabled-bg: transparent;

$navbar-default-brand-color: $navbar-default-link-color;
$navbar-default-brand-hover-color: $brand-info;
$navbar-default-brand-hover-bg: lighten($brand-offwhite, 4%);

$navbar-default-toggle-hover-bg: rgb(221, 221, 221);
$navbar-default-toggle-icon-bar-bg: rgb(136, 136, 136);
$navbar-default-toggle-border-color: $brand-primary;
.navbar a {font-family: $roboto-slab;}
.navbar button {
  margin:26px 0 7px;
}
.navbar-brand img{
  height: auto;
  width: auto;
  max-width: 358px;
  min-width: 182px;
  max-height: 34px;
}
//XS mobile viewport breakpoint 480px
@media (max-width: $screen-xs) {
  .navbar-brand img{
    width: 181px;
    height: auto;
  }
}

$nav-link-padding: 10px 15px;
$nav-link-hover-bg: white;
$nav-disabled-link-color: $gray-light;
$nav-disabled-link-hover-color: $gray-light;
$nav-tabs-border-color: rgb(221, 221, 221);
$nav-tabs-link-hover-border-color: $gray-lighter;
$nav-tabs-active-link-hover-bg: $body-bg;
$nav-tabs-active-link-hover-color: $gray;
$nav-tabs-active-link-hover-border-color: rgb(221, 221, 221);
$nav-tabs-justified-link-border-color: rgb(221, 221, 221);
$nav-tabs-justified-active-link-border-color: $body-bg;
$nav-pills-border-radius: $border-radius-base;
$nav-pills-active-link-hover-bg: $component-active-bg;
$nav-pills-active-link-hover-color: $component-active-color;

$pagination-color: $link-color;
$pagination-bg: rgb(255, 255, 255);
$pagination-border: rgb(221, 221, 221);
$pagination-hover-color: $link-hover-color;
$pagination-hover-bg: $gray-lighter;
$pagination-hover-border: rgb(221, 221, 221);
$pagination-active-color: rgb(255, 255, 255);
$pagination-active-bg: $brand-primary;
$pagination-active-border: $brand-primary;
$pagination-disabled-color: $gray-light;
$pagination-disabled-bg: rgb(255, 255, 255);
$pagination-disabled-border: rgb(221, 221, 221);
$pager-bg: $pagination-bg;
$pager-border: $pagination-border;
$pager-border-radius: 15px;
$pager-hover-bg: $pagination-hover-bg;
$pager-active-bg: $pagination-active-bg;
$pager-active-color: $pagination-active-color;
$pager-disabled-color: $pagination-disabled-color;

$jumbotron-padding: 30px;
$jumbotron-color: $brand-offwhite;
$jumbotron-bg: $gray-light;
$jumbotron-heading-color: $brand-offwhite;
$jumbotron-font-size: ceil(($font-size-base * 1.5));
$jumbotron-heading-font-size: ceil(($font-size-base * 4.5));
.jumbotron {}

$state-success-text: rgb(60, 118, 61);
$state-success-bg: rgb(223, 240, 216);
$state-success-border: darken($state-success-bg, 5%);
$state-info-text: rgb(49, 112, 143);
$state-info-bg: rgb(217, 237, 247);
$state-info-border: darken($state-info-bg, 7%);
$state-warning-text: rgb(138, 109, 59);
$state-warning-bg: rgb(252, 248, 227);
$state-warning-border: darken($state-warning-bg, 5%);
$state-danger-text: rgb(169, 68, 66);
$state-danger-bg: rgb(242, 222, 222);
$state-danger-border: darken($state-danger-bg, 5%);

$tooltip-max-width: 200px;
$tooltip-color: rgb(255, 255, 255);
$tooltip-bg: rgb(0, 0, 0);
$tooltip-opacity: .9;
$tooltip-arrow-width: 5px;
$tooltip-arrow-color: $tooltip-bg;

$popover-bg: rgb(255, 255, 255);
$popover-max-width: 276px;
$popover-border-color: rgba(0, 0, 0, 0.2);
$popover-fallback-border-color: rgb(204, 204, 204);
$popover-title-bg: darken($popover-bg, 3%);
$popover-arrow-width: 10px;
$popover-arrow-color: $popover-bg;
$popover-arrow-outer-width: ($popover-arrow-width + 1);
$popover-arrow-outer-color: fadein($popover-border-color, 5%);
$popover-arrow-outer-fallback-color: darken($popover-fallback-border-color, 20%);

$label-default-bg: $gray-light;
$label-primary-bg: $brand-primary;
$label-success-bg: $brand-success;
$label-info-bg: $brand-info;
$label-warning-bg: $brand-warning;
$label-danger-bg: $brand-danger;
$label-color: rgb(255, 255, 255);
$label-link-hover-color: rgb(255, 255, 255);
.label {font-family: $font-family-base; text-transform: capitalize;}

$modal-inner-padding: 15px;
$modal-title-padding: 15px;
$modal-title-line-height: $line-height-base;
$modal-content-bg: rgb(255, 255, 255);
$modal-content-border-color: rgba(0, 0, 0, 0.2);
$modal-content-fallback-border-color: rgb(153, 153, 153);
$modal-backdrop-bg: rgb(0, 0, 0);
$modal-backdrop-opacity: .5;
$modal-header-border-color: rgb(229, 229, 229);
$modal-footer-border-color: $modal-header-border-color;
$modal-lg: 900px;
$modal-md: 600px;
$modal-sm: 300px;

$alert-padding: 15px;
$alert-border-radius: $border-radius-base;
$alert-link-font-weight: bold;
$alert-success-bg: $state-success-bg;
$alert-success-text: $state-success-text;
$alert-success-border: $state-success-border;
$alert-info-bg: $state-info-bg;
$alert-info-text: $state-info-text;
$alert-info-border: $state-info-border;
$alert-warning-bg: $state-warning-bg;
$alert-warning-text: $state-warning-text;
$alert-warning-border: $state-warning-border;
$alert-danger-bg: $state-danger-bg;
$alert-danger-text: $state-danger-text;
$alert-danger-border: $state-danger-border;

$progress-bg: rgb(245, 245, 245);
$progress-bar-color: rgb(255, 255, 255);
$progress-border-radius: $border-radius-base;
$progress-bar-bg: $brand-primary;
$progress-bar-success-bg: $brand-success;
$progress-bar-warning-bg: $brand-warning;
$progress-bar-danger-bg: $brand-danger;
$progress-bar-info-bg: $brand-info;

$list-group-bg: rgb(255, 255, 255);
$list-group-border: rgb(221, 221, 221);
$list-group-border-radius: $border-radius-base;
$list-group-hover-bg: rgb(245, 245, 245);
$list-group-active-color: $component-active-color;
$list-group-active-bg: $component-active-bg;
$list-group-active-border: $list-group-active-bg;
$list-group-active-text-color: lighten($list-group-active-bg, 40%);
$list-group-disabled-color: $gray-light;
$list-group-disabled-bg: $gray-lighter;
$list-group-disabled-text-color: $list-group-disabled-color;
$list-group-link-color: rgb(85, 85, 85);
$list-group-link-hover-color: $list-group-link-color;
$list-group-link-heading-color: rgb(51, 51, 51);

$panel-bg: rgb(255, 255, 255);
$panel-body-padding: 15px;
$panel-heading-padding: 10px 15px;
$panel-footer-padding: $panel-heading-padding;
$panel-border-radius: $border-radius-base;
$panel-inner-border: none;
$panel-footer-bg: rgb(245, 245, 245);
$panel-default-text: $gray-dark;
$panel-default-border: rgb(221, 221, 221);
$panel-default-heading-bg: rgb(245, 245, 245);
$panel-primary-text: rgb(255, 255, 255);
$panel-primary-border: $brand-primary;
$panel-primary-heading-bg: $brand-primary;
$panel-success-text: $state-success-text;
$panel-success-border: $state-success-border;
$panel-success-heading-bg: $state-success-bg;
$panel-info-text: $state-info-text;
$panel-info-border: $state-info-border;
$panel-info-heading-bg: $state-info-bg;
$panel-warning-text: $state-warning-text;
$panel-warning-border: $state-warning-border;
$panel-warning-heading-bg: $state-warning-bg;
$panel-danger-text: $state-danger-text;
$panel-danger-border: $state-danger-border;
$panel-danger-heading-bg: $state-danger-bg;

$thumbnail-padding: 4px;
$thumbnail-bg: $body-bg;
$thumbnail-border: rgb(221, 221, 221);
$thumbnail-border-radius: $border-radius-base;
$thumbnail-caption-color: $text-color;
$thumbnail-caption-padding: 9px;

$well-bg: rgb(245, 245, 245);
$well-border: darken($well-bg, 7%);

$badge-color: rgb(255, 255, 255);
$badge-link-hover-color: rgb(255, 255, 255);
$badge-bg: $gray-light;
$badge-active-color: $link-color;
$badge-active-bg: rgb(255, 255, 255);
$badge-font-weight: bold;
$badge-line-height: 1;
$badge-border-radius: 10px;

$breadcrumb-padding-vertical: 8px;
$breadcrumb-padding-horizontal: 15px;
$breadcrumb-bg: rgb(245, 245, 245);
$breadcrumb-color: rgb(204, 204, 204);
$breadcrumb-active-color: $gray-light;
$breadcrumb-separator: "/";

$carousel-text-shadow: 0 1px 2px rgba(0,0,0,.6);
$carousel-control-color: rgb(255, 255, 255);
$carousel-control-width: 15%;
$carousel-control-opacity: .5;
$carousel-control-font-size: 20px;
$carousel-indicator-active-bg: rgb(255, 255, 255);
$carousel-indicator-border-color: rgb(255, 255, 255);
$carousel-caption-color: rgb(255, 255, 255);

$close-font-weight: bold;
$close-color: rgb(0, 0, 0);
$close-text-shadow: 0 1px 0 #fff;
$code-color: rgb(199, 37, 78);
$code-bg: rgb(249, 242, 244);
$kbd-color: rgb(255, 255, 255);
$kbd-bg: rgb(51, 51, 51);
$pre-bg: rgb(245, 245, 245);
$pre-color: $gray-dark;
$pre-border-color: rgb(204, 204, 204);
$pre-scrollable-max-height: 340px;
$component-offset-horizontal: 180px;
$text-muted: $gray-light;
$abbr-border-color: $gray-light;
$headings-small-color: $gray-light;
$blockquote-small-color: $gray-light;
$blockquote-font-size: ($font-size-base * 1.25);
$blockquote-border-color: $gray-lighter;
$page-header-border-color: $gray-lighter;
$dl-horizontal-offset: $component-offset-horizontal;
$dl-horizontal-breakpoint: $grid-float-breakpoint;
$hr-border: $gray-lighter;

h2 a.page-anchor {
    padding-top: 80px;
}
